@import url(https://fonts.googleapis.com/css2?family=Lora:wght@500&display=swap);
body {
  margin: 0;
  background-color: black;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

:root{
  --syca-yellow: #988345;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.header {
  padding-top: 15px;
  background: black/*#F7D8BA*//*#CAF1DE*/;
}

.nav-content {
  padding: 0 2rem;
  margin: 0 auto;
  display: flex;
  justify-content: space-evenly;
}

.nav-item {
  display: inline;
  margin-left: 2.5rem;
  color: #988345;
  color: var(--syca-yellow)/*#3b455f*/;
  font-weight: 500;
}

.nav-item:hover {
  color: white;
  cursor: pointer;
}

.myName {
  font-size: 20px;
  color: #988345;
  color: var(--syca-yellow);
  font-weight: 700;
}

.Intro{
    background-color: black/*#ACDDDE*/;
    border-bottom: 1px solid var(--syca-yellow);
}

.greeting{
    padding-top: 9.5rem;
    font-size: large;
    margin: 0;
    color: var(--syca-yellow);
    font-weight: 500;
}

.name{
    font-size: 6.3rem;
    margin: 0;
    font-family: 'Lora' , sans-serif;
    letter-spacing: 0.3rem;
    padding-left: 40px;
    color: var(--syca-yellow);
}

.box{
    width:70px;
    height: 5px;
    background: var(--syca-yellow);
    position: absolute;
    left: 47%;
}

.roles{
    font-size: 1.3rem;
    letter-spacing: 0.1rem;
    color: var(--syca-yellow);
    margin-top: 40px;
    margin-bottom: 60px;
    font-weight: 500;
}

.About{
    background-color: black;
    border-bottom: 1px solid var(--syca-yellow);
}
#myImage{
    margin: 5rem 0;
    height: 380px;
    width: 380px;
    border-radius: 200px;
}
#my-rutgers{
    text-decoration: none;
    color: var(--syca-yellow);
}
#my-rutgers:hover{
    color: white;
}
.heading{
    color: var(--syca-yellow);
    font-size: 3.5rem;
    font-family: 'Lora', sans-serif;
    margin: 2.5rem 0;
}

.p1{
    color: var(--syca-yellow);
    padding: 0 1.5rem ;
    font-size: 19px;
    font-weight: 500;
}

.Experience{
    background-color: black;
    color: var(--syca-yellow);
    border-bottom: 1px solid var(--syca-yellow);
}

.exp-heading{
    padding: 2.5rem 0;
    font-size: 3.5rem;
    font-family: 'Lora', sans-serif;
    margin: 0;
}

#att-img{
    border-radius: 100px;
    width: 250px;
    height: 120px;
    margin: 7rem;
}

.my-exp{
    padding-left: 3rem;
    padding-top: 2rem;
}
.exp-des-1{
    padding-top: 2rem;
}
.exp-des-1, .exp-des-2, .exp-des-3{
    padding-bottom: 2rem;
    margin: 0;
    font-weight: 500;
    font-size: 1.1rem;
}
.Contact {
    background-color: black;
    padding: 0 10rem;
}

.footer-heading{
    color: var(--syca-yellow);
    font-size: 3.5rem;
    margin: 2.5rem 0 1rem 0; 
    font-family: 'Lora', sans-serif;
}

.footer-text{
    font-size: 1.2rem;
    font-weight: 500;
    color: var(--syca-yellow);
    margin: 2rem 0;
}

.react-icons{
    margin: 20px 15px;
}

@media screen and (max-width: 700px){
    .Contact{
        padding: 0 1rem;
    }
}
